<template>
    <div>
        <el-form ref="form" :model="form" label-width="130px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="供应商名称" prop="supplier_name">
                        <el-input v-model="form.supplier_name" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="供应商类型" prop="dict_id">
                        <el-select v-model="form.dict_id" placeholder="请选择" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsSupplierTypes"
                                :key="item.dict_id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="联系人" prop="contact">
                        <el-input v-model="form.contact" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="移动电话" prop="mobile">
                        <el-input v-model="form.mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="地址" prop="address">
                        <el-input v-model="form.address" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="收款账号">
                        <el-input v-model="form.receipt_account" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="收款银行">
                        <el-input v-model="form.receipt_bank" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　认证信息" name="2">
                    
                    <el-form-item label="营业执照照片">
                        <el-upload
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess"
                            :data="{type:5}"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.url" :src="form.url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="统一社会信用代码">
                                <el-input v-model="form.credit_code"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                optionsSupplierTypes: [],
                visibleEarnest: false,
                pageType: 'add',
                form: {
                    supplier_name:'',
                    dict_id:'',
                    dict_type:'',
                    contact:'',
                    mobile:'',
                    address:'',
                    receipt_account:'',
                    receipt_bank:'',
                    credit_code:'',
                    operation_uniqid:'',
                    url:''
                },
                rules: {
                    supplier_name: [
                        { required: true, message: '请输入供应商名称', trigger: 'blur' }
                    ],
                    dict_id: [
                        { required: true, message: '请选择供应商类型', trigger: 'change' }
                    ],
                    contact: [
                        { required: true, message: '请输入联系人', trigger: 'blur' }
                    ],
                    mobile: [
                        { required: true, message: '请输入移动电话', trigger: 'blur' }
                    ],
                    address: [
                        { required: true, message: '请输入地址', trigger: 'blur' }
                    ],
                }
            }
        },
        mounted(){
            this.checkPath()
            this.getSupplierTypes()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {

                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.pageType = 'view';
                    this.getFormData();
                }
            },
            getFormData(){
                API.apiNewCarView({id: this.$route.query.id, operation_uniqid: this.$route.query.operation_uniqid}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            getSupplierTypes(){
                API.apiNewCarSupplierTypes({}).then(res => {
                    this.optionsSupplierTypes = res.data
                })
            },
            handleUploadSuccess(res, file, fileList){
                this.form.url = res.data.url
                this.form.credit_code = res.data.register_number
            },
            handleBeforeUpload(){

            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiNewCarAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.go(-1)
                            }) 
                        }else{
                            API.apiNewCarEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.go(-1)
                            })
                        }
                    }else{
                        return false
                    }
                })
                

            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>